import React, { useContext } from 'react';
import { Jumbotron, Button, Container, InputGroup, FormControl, Row, Col } from "react-bootstrap"
import { PostContext } from "../Context/PostContext"
import "../Css/Banner.css"

const Baner = ({ id }) => {

    const { posts } = useContext(PostContext)
    const post = posts.find(post => post.id == id)

    return (

        <Container className="text-center container-padding">
            <Row>
                <Col>
                    <Jumbotron className="jumbo">
                        {post ? <h2>Приклучи се на {post.people} ентузијасти и учи {post.name}.</h2> : <h2>Приклучи се на 1350 ентузијасти и учи дигитални вештини.</h2>}
                        <h2>Бесплатно.</h2>
                        <InputGroup className="mb-3">
                            <FormControl
                                placeholder="Email Address"
                                aria-label="Recipient's username"
                                aria-describedby="basic-addon2"
                            />
                            <InputGroup.Append>
                                <Button type="submit" variant="outline-secondary" className="email-btn">Пријави се</Button>
                            </InputGroup.Append>
                        </InputGroup>
                        <p>Можеш да се исклучиш од листата на маилови во секое време!</p>
                    </Jumbotron>
                </Col>
            </Row>
        </Container>
    );
}

export default Baner;