import React, { useContext } from 'react';
import { Card, Container, Col, Row, Button } from "react-bootstrap"
import { PostContext } from "../Context/PostContext"
import { faHome, faArrowRight } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "../Css/LessonDetails.css"

const LessonsDetails = ({ id }) => {

    const { posts } = useContext(PostContext)
    const post = posts.find(post => post.id == id)

    return (

        <Container>
            <Row className="row-details">
                <Col md="7">
                    <Row >
                        {post.lesson.map((el) => (
                            <Col md="12" className="card-col" key={el.lessonId}>
                                <a className="link" href={el.link} target="_blank" variant="secondary">
                                    <Card>
                                        <Card.Body>
                                            <Card.Title># {el.title} <p className="para-1">{el.date}</p></Card.Title>
                                            <Card.Text>{el.description}</Card.Text>
                                        </Card.Body>
                                    </Card>
                                </a>
                            </Col>

                        ))}
                    </Row>
                </Col>

                <Col md="5">
                    <Card >
                        <Card.Body>
                            <Card.Title><h5>Приклучи се на {post.people} ентузијасти и учи {post.name}.</h5></Card.Title>
                            <Card.Text>
                                {post.description}
                            </Card.Text>
                            <Button  href={post.link1} className="btn-more" variant="outline-primary">Повеќе <FontAwesomeIcon className="arrow" icon={faArrowRight} /></Button>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Container>
    );
}

export default LessonsDetails;