import React from 'react';
import Baner from './Baner';
import LessonsDetails from "./LessonsDetails"
import Cards from './Cards';

const routes = {
    "/": () =>
        <div>
            <Baner />
            <Cards />
        </div>,

    "/Detail/:id": ({ id }) =>
        <div>
            <Baner id={id} />
            <LessonsDetails id={id} />
        </div>
}

export default routes
