import React, { useState, useContext, useEffect } from 'react';
import { Container, Row, Col, Button } from "react-bootstrap"
import Card1 from "./Card1"
import { PostContext } from '../Context/PostContext';
import "../Css/Cards.css"

const Cards = () => {

    const { posts, categories, selected, setSelected, btn, setBtn } = useContext(PostContext)
    const arrCard = posts.filter(el => el.name !== "")
    const [newArrCard, setNewArrCard] = useState([])

    const setList = (itemCategory) => {

        let items = selected.findIndex(el => el == itemCategory)

        if (items > -1) {
            let arr = selected.slice()
            arr.splice(items, 1)
            setSelected(arr)
        } else {
            setSelected([...selected, itemCategory])
        }
    }

    useEffect(() => {
        if (selected.length === 0) {
            setNewArrCard(arrCard)
        } else {
            setNewArrCard(arrCard.filter(el => selected.includes(el.name)))
        }

        if (selected.length === arrCard.length) {
            setSelected([])
        }
    }, [selected])

    const showAll = () => {
        setSelected([]);
        setNewArrCard(arrCard)
    }


    return (
        <Container>
            <Row>
                <Col className="text-center card-col">
                    <h3>Филтрирај по категорија</h3>
                    <label className="switch">
                        <input type="checkbox" checked={btn} onChange={() => setBtn(!btn)} />
                        <span className="slider round">{btn ? <div className="cube"></div> : null}</span>
                    </label>
                </Col>
            </Row>

            <Row className="row-btn">
                <Button variant="outline-secondary" className={`btn-card ${(categories.length === newArrCard.length) ? 'active' : ''}`} onClick={showAll} >Сите</Button>
                {categories.map((card, i) => {
                    return (
                        <Button variant="outline-secondary" className={`btn-card ${(selected.includes(card) && categories.length != newArrCard.length) ? 'active' : ''}`} key={i} onClick={() => {
                            setList(card)
                        }}>{card}</Button>
                    )
                })}
            </Row>

            <Row className={btn ? "card-row " : "justify-content-md-center"}>
                {newArrCard.map((el) => (
                    <Card1 key={el.id} album={el.albums && el.albums} id={el.id} name={el.name} description={el.description} imageName={el.image} lesson={el.lesson && el.lesson} btn={btn} />
                ))}
            </Row>
        </Container>
    );
}

export default Cards;