import React from 'react';
import { Card, Col } from "react-bootstrap"
import { navigate } from "hookrouter"
import "../Css/Card1.css"

const Card1 = (props) => {

    const navigateTo = () => {
        navigate(`/Detail/${props.id}`)
        window.scrollTo({top:0})
    }

    return (

        <Col lg={props.btn ? "4" : "8"} md={props.btn ? "6" : "8"} onClick={navigateTo}>
            <Card className={props.btn ? "card-11 morph" : "card-1 morph"}>
                {props.btn ? <Card.Img variant="top" className="img-card" src={require(`../Images/${props.imageName}.jpg`)} alt="img" /> : null}
                <Card.Body className={props.btn ? null : "card-body-1"}>
                    {props.btn ? null : <p className="para">{props.lesson.length} лекции</p>}
                    <Card.Title style={{ textTransform: 'capitalize' }}>{props.name}</Card.Title>
                    <Card.Text>
                        {props.description}
                    </Card.Text>
                    {props.btn ? <Card.Footer className="text-muted card-footer">{props.lesson.length} лекции</Card.Footer> : null}
                </Card.Body>
            </Card>
        </Col>
    );
}

export default Card1;