import React from 'react';
import { Navbar, Nav, Button, Container } from "react-bootstrap"
import 'bootstrap/dist/css/bootstrap.min.css';
import Popup from "reactjs-popup";
import "../Css/Header.css"

const Header = () => {

    return (
        <Container fluid className='con-flu'>
            <Navbar bg="light" expand="lg">
                <Navbar.Brand href="/">
                    <img className="imgNav" src={require(`../Images/brainster.png`)} alt="img" />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <Nav.Link href="https://brainster.co/courses?type=academy">Академии</Nav.Link>
                        <Nav.Link href="https://brainster.co/courses?type=course">Вебинари</Nav.Link>
                        <Nav.Link href="https://blog.brainster.co/category/%d0%ba%d0%b0%d1%80%d0%b8%d0%b5%d1%80%d0%b0/">Тест за кариера</Nav.Link>
                        <Nav.Link href="https://blog.brainster.co/">Блог</Nav.Link>
                    </Nav>
                    <Popup trigger={<Button className="btn-nav">Пријави се</Button>} position="bottom center">
                        <div >
                            <input className="inp" type="email" required placeholder="Email Address" />
                            <Button type="submit" size="sm" className="btn-popup">Испрати</Button>
                        </div>
                    </Popup>
                </Navbar.Collapse>
            </Navbar>
        </Container>
    );
}

export default Header;